<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>信息管理</el-breadcrumb-item>
        <el-breadcrumb-item class="list_back"><span @click.stop="ToList">留言管理</span> </el-breadcrumb-item>
        <el-breadcrumb-item v-if="pageType === 2">查看留言</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <table-item ref="message" :pageId="pageId" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getTitle="getTitle" @getPageType="getPageType" @loadData="loadData"/>
  </div>
</template>

<script>
import TableItem from '../../components/table-item/TableItem'
export default {
  components: { TableItem },
  data() {
    return {
      queryForm: {
        title: '',
        status:'',//留言状态 0-未回复，1-已回复
        flag:'',//是否显示
        total: '',
        page: 1,
        size: 10
      },
      dataInfo: [],
      pageType: 0,
      pageId: 3
    }
  },

  methods: {
    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getTitle(e) {
      this.queryForm.title = e
      this.loadData()
    },

    getPageType(e) {
      this.pageType = e
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/message/query/', this.queryForm).then(res => {
        this.dataInfo = res.rows
        this.queryForm.total = res.total
      })
    },

    // 返回列表
    ToList() {
      this.$refs.message.pageType = 0
    }
  },
  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.list_back {
  font-weight: bold;
  cursor: pointer;
}
</style>
