<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>信息管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standServe' }">站点设置</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <!-- <el-form-item label="站点名称" prop="siteName">
          <el-input v-model="ruleForm.siteName"></el-input>
        </el-form-item>
        <el-form-item label="站点logo图片" prop="siteLogo">
          <el-upload
            class="avatar-uploader-logo"
            :action="uploadAction()"
            :show-file-list="false"
            :on-success="(res, file, fileList) => handleAvatarSuccess(res, file, fileList, 0)"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="ruleForm.siteLogo" :src="pictureBaseUrl + ruleForm.siteLogo" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"><i></i></i>
          </el-upload>
          <div class="font_size">
            <span>支持jpg／png格式，建议尺寸： 519 * 74</span>
          </div>
        </el-form-item> -->
        <el-form-item label="站点banner图片" prop="siteBanner">
          <!-- <el-upload :class="'avatar-uploader-' + localList[index].className" :action="uploadAction()" :show-file-list="false" :on-success="localList[index].successEvent" :before-upload="beforeAvatarUpload"> -->
          <el-upload
            class="avatar-uploader-banner"
            :action="uploadAction()"
            :show-file-list="false"
            :on-success="(res, file, fileList) => handleAvatarSuccess(res, file, fileList, 1)"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="ruleForm.siteBanner" :src="pictureBaseUrl + ruleForm.siteBanner" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"><i></i></i>
          </el-upload>
          <div class="font_size">
            <span>支持jpg／png格式，建议尺寸： 1920 * 318</span>
            <!-- <span class="tips">小程序首页，头部切换门店及搜索栏背景图</span> -->
          </div>
        </el-form-item>
        <el-form-item label="联系方式" prop="siteContact">
          <el-input type="textarea" v-model="ruleForm.siteContact" placeholder="请输入联系方式"></el-input>
        </el-form-item>
        <el-form-item label="下载提醒" prop="downloadSuccess">
          <el-input type="textarea" v-model="ruleForm.downloadSuccess" placeholder="请输入下载提醒语句"></el-input>
        </el-form-item>
        <el-form-item label="无法下载提示" prop="downloadError">
          <el-input type="textarea" v-model="ruleForm.downloadError" placeholder="请输入无法下载提示语句"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
        </el-form-item>
      </el-form>
      <!-- 表格 -->
      <!-- <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" :selectable="isDisabled" disabled="true" width="55"> </el-table-column>
        <el-table-column prop="moduleName" label="模块名称"> </el-table-column>
      </el-table> -->
      <!-- <el-button type="primary">保存</el-button> -->
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // pictureBaseUrl: this.$picDownload,
      pictureBaseUrl: this.$picDownload,
      ruleForm: {
        siteName: '',
        siteLogo: '',
        siteBanner: '',
        siteContact: '',
        downTips: '',
        downloadSuccess: '',
        downloadError: ''
      },
      rules: {
        siteName: [
          { required: true, message: '请输入站点名称', trigger: 'blur' },
          { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
        ],
        siteLogo: [{ required: true, message: '请上传logo图片', trigger: 'blur' }],
        siteBanner: [{ required: true, message: '请上传banner图片', trigger: 'blur' }]
      },
      tableData: [],
      multipleSelection: []
    }
  },
  methods: {
    // 表格-----------------------------------------------------
    isDisabled(row, index) {
      return row.isDisabled === 0 ? true : false // 0 可选 1 禁用
    },

    toggleSelection(rows) {
      if (rows) {
        this.$nextTick(() => {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row)
          })
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 表格-----------------------------------------------------

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.httpPost('/globalConfig/save', this.ruleForm).then(ret => {
            this.$message({ type: 'success', message: '保存成功!' })
            this.loadData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm(formName) {
      this.$refs[formName].resetFields()
      this.$router.back()
    },

    handleAvatarSuccess(res, file, fileList, index) {
      console.log(res, file, fileList, index)
      if (!res) return
      this.isUpload = false
      if (res.code == '0') {
        if (index === 0) {
          this.ruleForm.siteLogo = res.data.fileUrl
        } else {
          this.ruleForm.siteBanner = res.data.fileUrl
        }
      }
    },

    uploadAction() {
      return this.$picUpload
      // return ''
    },

    beforeAvatarUpload(file) {
      this.isUpload = true
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG / png 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },

    loadData() {
      this.httpGet('/globalConfig/query', '').then(res => {
        this.ruleForm = res
      })
    }
  },
  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-input input {
  width: 400px;
}

/deep/.el-upload--text {
  width: auto;
  display: block;
  height: auto;
}
.avatar-uploader-logo {
  width: 519px;
  .avatar-uploader-icon {
    width: 519px;
    height: 74px;
    line-height: 74px;
  }
  img {
    width: 519px;
    height: 74px;
  }
}
.avatar-uploader-banner {
  width: 640px;
  .avatar-uploader-icon {
    width: 640px;
    height: 106px;
    line-height: 106px;
  }
  img {
    width: 640px;
    height: 106px;
  }
}
/deep/.el-table {
  .el-table__header-wrapper {
    .el-checkbox {
      display: none;
    }
  }
  .cell {
    text-align: center;
  }
}
.el-button {
  margin: 20px 0;
}
/deep/.el-form-item {
  .el-textarea {
    width: 590px;
  }
}
</style>
