<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>信息管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/column' }">栏目管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'add' ? '新增栏目' : '修改栏目' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="栏目名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <!--<el-form-item label="位置" prop="position">-->
          <!--<el-select v-model="ruleForm.position" placeholder="请选择顺序">-->
            <!--<el-option label="上" value="1"></el-option>-->
            <!--<el-option label="下" value="2"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="顺序" prop="orderNo">
          <el-input v-model="ruleForm.orderNo" @keyup.native="proving" placeholder="请输入正整数"></el-input>
        </el-form-item>
        <!--<el-form-item label="显示类型" prop="showType">-->
          <!--<el-select v-model="ruleForm.showType" placeholder="请选择显示类型">-->
            <!--<el-option label="download路径" :value="1"></el-option>-->
            <!--<el-option label="页面链接" :value="2"></el-option>-->
            <!--<el-option label="固定功能" :value="3"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="栏目内容" prop="content">
          <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="cancelForm('ruleForm')">取消</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  data() {
    return {
      ruleForm: {
        name: '',
        position: '1',
        orderNo: '',
        showType: '2',
        delivery: false,
        content: '',
        menuType: 1
      },
      rules: {
        name: [
          { required: true, message: '请输入栏目名称', trigger: 'blur' },
          { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
        ],
        position: [{ required: true, message: '请选择顺序', trigger: 'change' }],
        orderNo: [{ required: true, message: '请输入正整数', trigger: 'change' }],
        showType: [{ required: true, message: '请选择显示类型', trigger: 'change' }],
        content: [{ required: true, message: '请输入栏目内容', trigger: 'change' }]
      }
    }
  },

  methods: {
    // 富文本
    getCont(val) {
      this.ruleForm.content = val
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.$route.params.type === 'add') {
            this.httpPost('/itemset/add/column', this.ruleForm).then(res => {
              this.$message.success('增加成功')
              this.cancelForm('ruleForm')
            })
          } else {
            this.httpPost('/itemset/mod/column/' + this.ruleForm.id, this.ruleForm).then(res => {
              this.$message.success('修改成功')
              this.cancelForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm(formName) {
      this.$refs[formName].resetFields()
      this.$router.back()
    },
    proving() {
      this.ruleForm.orderNo = this.ruleForm.orderNo.replace(/[^\.\d]/g, '')
      this.ruleForm.orderNo = this.ruleForm.orderNo.replace('.', '')
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      var info = this.$sessionData('get', 'columnInfo')
      this.ruleForm = info
      this.$refs.bzlc.editorValue = info.content
      this.ruleForm.position = this.ruleForm.position.toString()
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/ .el-input input {
  width: 400px;
}
</style>
