<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>信息管理</el-breadcrumb-item>
        <el-breadcrumb-item>栏目管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_fn_btn"></div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button class="add_btn" @click.stop="addColumn" icon="el-icon-plus">新增栏目</el-button>
          <!-- <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="batchGroup">批量分组</span>
            </div>
          </div> -->
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="栏目名称"> </el-table-column>
          <el-table-column prop="position" label="位置">
            <template slot-scope="scope">
              {{ scope.row.position === 1 ? '上' : '下' }}
            </template>
          </el-table-column>
          <el-table-column prop="orderNo" label="顺序"> </el-table-column>
          <el-table-column prop="showType" label="显示类型">
            <template slot-scope="scope">
              {{ scope.row.showType === 1 ? 'download路径' : scope.row.showType === 2 ? '页面链接' : '固定功能' }}
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        menuType: 1,
        total: '',
        page: 1,
        size: 10
      },
      tableData: []
    }
  },

  methods: {
    // 增加
    addColumn() {
      this.$router.push({ path: '/editColumn', name: 'EditColumn', params: { type: 'add' } })
    },

    // 修改
    edit(item) {
      this.$router.push({ path: '/editColumn', name: 'EditColumn', params: { type: 'edit', info: item } })
      this.$sessionData('set', 'columnInfo', item)
    },

    // 删除
    del(id) {
      console.log(id)
      this.$confirm('此操作将删除该条数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/itemset/rem/column/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/itemset/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
