<template>
  <div>
    <!-- 内容主体 -->
    <el-card>
      <div class="main_cont" v-if="pageType === 0">
        <!-- 功能按钮 -->
        <div class="MT_fn_btn">
          <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
            <el-form-item>
              <el-input v-model="queryForm.title" placeholder="请输入标题名称"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
            </el-form-item>
          </el-form>
          <div class="btn_left"></div>
        </div>

        <div class="MT_table_cont">
          <!-- 按钮 -->
          <div class="btns">
            <el-button v-if="$route.path !== '/message'" @click.stop="add" icon="el-icon-plus">新增</el-button>
            <div class="btn_cont">
              <div class="choose_btn _color">
                <span v-if="$route.path === '/message'"  @click.stop="removeMessage">批量删除</span>
                <span v-else @click.stop="remove">批量删除</span>
                <span v-if="$route.path !== '/message'" @click.stop="push">批量推送</span>
              </div>
            </div>
          </div>
          <!-- 表格列表 -->
          <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column prop="rownum_" label="编号" width="100"> </el-table-column>
            <el-table-column v-if="$route.path === '/message'" prop="name" label="留言人" width="180"> </el-table-column>
            <el-table-column prop="title" label="标题"> </el-table-column>
            <el-table-column v-if="$route.path !== '/message'" prop="hits" label="点击数" width="120"> </el-table-column>
            <el-table-column v-else label="留言状态" width="120">
              <template slot-scope="scope">
                {{ scope.row.status === 0 ? '未回复' : '已回复' }}
              </template>
            </el-table-column>
            <el-table-column label="是否已发布" width="120">
              <template slot-scope="scope">
                {{ scope.row.ifPublish === 1 ? '是' : '否' }}
              </template>
            </el-table-column>
            <el-table-column v-if="$route.path !== '/message'" prop="createDate" label="更新时间" width="180"> </el-table-column>
            <el-table-column v-else prop="createDate" label="发表时间" width="180"> </el-table-column>
            <!-- 操作 -->
            <el-table-column label="操作" width="100">
              <template slot-scope="scope">
                <el-link v-if="$route.path === '/message'" :underline="false" type="primary" @click.stop="edit(scope.row)">查看</el-link>
                <el-link v-else :underline="false" type="primary" @click.stop="edit(scope.row)">编辑</el-link>
                <el-link v-if="$route.path === '/message'" :underline="false" type="danger" @click.stop="delMessage(scope.row.id)">删除</el-link>
                <el-link v-else :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
              </template>
            </el-table-column>
          </el-table>

          <!-- 分页器 -->
          <div class="MT_pagination" v-if="queryForm.total">
            <span class="jump_first" @click.stop="$jumpToFirst(queryForm, changePage)">首页</span>
            <span class="jump_last" @click.stop="$jumpToLast(queryForm, changePage)">尾页</span>
            <el-pagination
              background
              :total="queryForm.total"
              :current-page.sync="queryForm.page"
              :page-size.sync="queryForm.size"
              @current-change="changePage"
              layout="total, prev, pager, next, jumper"
            />
          </div>
        </div>
      </div>

      <!-- 新增/修改 -->
      <add-edit v-if="pageType === 1 || pageType === 2" :pageType="pageType" :info="currentInfo" @changePageType="changePageType" :type="type"/>

      <!-- 推送对话框 -->
      <el-dialog title="数据推送" :visible.sync="dialogFormVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="form">
          <el-form-item label="推送平台" :label-width="formLabelWidth">
            <el-select v-model="form.region" placeholder="请选择推送平台">
              <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="pushStand">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import AddEdit from '@/components/add-edit/AddEdit'
export default {
  components: { AddEdit },
  props: ['pageId', 'dataInfo', 'queryForm','type'], // 0 公告 1 新闻 2 法律法规 3 留言
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      pageType: 0, //  0 列表 1 增加 2 修改 3 删除
      currentInfo: '',
      dialogFormVisible: false,
      form: {
        region: ''
      },
      formLabelWidth: '120px',
      platformList: []
    }
  },

  methods: {
    pushStand() {
      let bulletinIdList =[]
      for(var i = 0;i<this.multipleSelection.length;i++){
        bulletinIdList.push(this.multipleSelection[i].id)
      }
      let params = {
        bulletinIdList:bulletinIdList,
        pushPlatformId:this.form.region
      }

      this.httpPost('/push/bulletin/add', params).then(ret => {
        this.$message({ type: 'success', message: '推送成功!' })
        this.$emit('loadData')
        this.dialogFormVisible = false
      })
    },
    platform() {

      this.httpGet('/push/platform/queryAll', '').then(res => {
        this.platformList = res
      })
    },
    // 新增
    add() {
      this.pageType = 1
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/bulletin/rem/batch', idList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },
    //批量删除信息
    removeMessage(){
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/message/rem', idList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },

    // 批量推送
    push() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要推送的数据！')
      this.dialogFormVisible = true
    },

    // 修改
    edit(item) {
      this.pageType = 2
      this.currentInfo = item
    },
    //删除信息
    delMessage(id){
      console.log(id)
      this.$confirm('此操作将删除该条数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList =[]
          idList.push(id)
          this.httpPost('/message/rem', idList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },
    // 删除
    del(id) {
      console.log(id)
      this.$confirm('此操作将删除该条数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/bulletin/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },

    changePageType(type) {
      this.pageType = type
      this.$emit('loadData')
    },

    // 页码改变
    changePage() {
      this.$emit('getPage', this.queryForm.page)
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 搜索
    onSubmit() {
      this.$emit('getTitle', this.queryForm.title)
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      this.tableData = newVal
    },

    pageType(newVal, oldVal) {
      this.$emit('getPageType', newVal)
    }
  },
  mounted() {
    this.platform()
  }
}
</script>

<style lang="scss" scoped></style>
