<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>信息管理</el-breadcrumb-item>
        <el-breadcrumb-item class="list_back"><span @click.stop="ToList">标准水平评价</span> </el-breadcrumb-item>
        <el-breadcrumb-item v-if="pageType === 1">新增信息</el-breadcrumb-item>
        <el-breadcrumb-item v-if="pageType === 2">修改信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <table-item ref="standBoard" :pageId="pageId" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getTitle="getTitle" @getPageType="getPageType" @loadData="loadData" type="1"/>
  </div>
</template>

<script>
  import TableItem from '../../components/table-item/TableItem'
  export default {
    components: { TableItem },
    data() {
      return {
        queryForm: {
          title: '',
          total: '',
          type:1,
          page: 1,
          size: 10
        },
        dataInfo: [],
        pageType: 0,
        pageId: 0
      }
    },

    methods: {

      getPage(e) {
        this.queryForm.page = e
        this.loadData()
      },

      getTitle(e) {
        console.log(e)
        this.queryForm.title = e
        this.loadData()
      },

      getPageType(e) {
        this.pageType = e
      },

      loadData() {
        this.queryForm.total = ''
        console.log(this.queryForm)
        this.httpGet('/bulletin/query/', this.queryForm).then(res => {
          this.dataInfo = res.rows
          this.queryForm.total = res.total
        })
      },

      // 返回列表
      ToList() {
        this.$refs.standBoard.pageType = 0
      }
    },
    mounted() {
      this.loadData()
    }
  }
</script>

<style lang="scss" scoped>
  .list_back {
    font-weight: bold;
    cursor: pointer;
  }
</style>
