<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>信息管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standServe' }">标准服务设置</el-breadcrumb-item>
        <el-breadcrumb-item>修改服务内容</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="服务名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="简介" prop="descr">
          <el-input type="textarea" v-model="ruleForm.descr"></el-input>
        </el-form-item>
        <el-form-item label="图片" prop="picture">
          <el-upload
            ref="uploadMainPicture"
            class="picMain"
            multiple
            :limit="1"
            :file-list="ruleForm.picture"
            list-type="picture-card"
            :action="uploadActionMain()"
            :on-remove="handleRemoveMain"
            :on-success="uploadSuccessMain"
            :before-upload="beforeUpload"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="栏目内容" prop="content">
          <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="cancelForm('ruleForm')">取消</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  data() {
    return {
      ruleForm: {
        name: '',
        descr: '',
        picture: [],
        content: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入栏目名称', trigger: 'blur' },
          { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
        ],
        descr: [
          { required: true, message: '请输入简介内容', trigger: 'blur' },
          { min: 3, max: 500, message: '长度在 3 到 500 个字符', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请输入栏目内容', trigger: 'blur' },
          { min: 3, max: 5000, message: '长度在 3 到 5000 个字符', trigger: 'blur' }
        ],
        picture: [{ required: true, message: '请上传图片', trigger: 'change' }]
      }
    }
  },

  methods: {
    uploadActionMain() {
      return this.$picUpload
      // return ''
    },
    // 上传图片成功
    uploadSuccessMain(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.picture = fileList
      if (this.ruleForm.picture.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
    },
    // 上传图片之前
    beforeUpload() {
      if (this.ruleForm.picture.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
    },
    handleRemoveMain(file, fileList) {
      console.log(fileList.length)
      console.log(this.ruleForm.picture.length)
      if (fileList.length != this.ruleForm.picture.length) {
        let fileName = file.response.data.fileUrl
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.picture.length; i < len; i++) {
          if (this.ruleForm.picture[i].response.data.fileUrl === fileName) {
            index = i
            break
          }
          console.log(index)
        }
        this.ruleForm.picture.splice(index, 1)
      }

      if (fileList.length < 1) {
        document.querySelector('.picMain .el-upload--picture-card').style.display = 'block'
      }
    },
    // 富文本
    getCont(val) {
      this.ruleForm.content = val
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm);
          if (params['picture'] && params['picture'].length > 0)
            params['itemUrl'] =params['picture'].map((o) => o.response.data.fileUrl).join(",")
            // params['itemUrl'] ='HebeUpload/W020120920251489263636.jpg'
          this.httpPost("/itemset/mod/service/"+this.ruleForm.id,params).then(res => {
            this.$message.success('修改成功')
            this.cancelForm('ruleForm')
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm(formName) {
      this.$refs[formName].resetFields()
      this.$router.back()
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.ruleForm = params.info
      this.ruleForm.picture = []
      this.$refs.bzlc.editorValue = params.info.content
      if (this.ruleForm['itemUrl']) {
          this.ruleForm.picture.push({
            response: {
              data:{
                fileUrl:this.ruleForm['itemUrl']
              }
            },
            url: this.$picDownload + this.ruleForm['itemUrl']
          })
        if (this.ruleForm.picture.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/ .el-input input {
  width: 400px;
}
/deep/.el-form-item {
  textarea {
    width: 940px;
  }
}
</style>
