<template>
  <el-form v-if="$route.path !== '/message'" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="标题" prop="title">
      <el-input size="small" style="width: 300px" v-model="ruleForm.title"></el-input>
    </el-form-item>
    <el-form-item label="内容" prop="content">
      <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
    </el-form-item>
    <el-form-item label="附件">
      <div class="upload_file">
        <el-upload
          class="upload-demo"
          :action="uploadAction()"
          :on-success="uploadSuccess"
          :beforeUpload="beforeExcelUpload"
          :on-remove="handleRemove"
          :file-list="ruleForm.attachPicList">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><i class="el-icon-upload2"></i> 上传</div>
        </el-upload>
        <div class="tips">选择您要上传的附件，大小限制为100MB</div>
      </div>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
      <el-button type="primary" @click="issueForm('ruleForm')">发布</el-button>
      <el-button type="primary" @click="cancelForm">取消</el-button>
      <!-- <el-button @click="resetForm('ruleForm')">重置</el-button> -->
    </el-form-item>
  </el-form>

  <!-- 留言 -->
  <el-form v-else :model="leaveForm" :rules="leaveRules" ref="leaveForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="留言人：" prop="name">
      <el-input disabled v-model="leaveForm.name"></el-input>
    </el-form-item>
    <el-form-item label="留言时间：" prop="createDate">
      <el-input disabled v-model="leaveForm.createDate"></el-input>
    </el-form-item>
    <el-form-item label="联系电话：" prop="contact">
      <el-input disabled v-model="leaveForm.contact"></el-input>
    </el-form-item>
    <el-form-item label="电子邮箱：" prop="email">
      <el-input disabled v-model="leaveForm.email"></el-input>
    </el-form-item>
    <el-form-item label="联系地址：" prop="address">
      <el-input disabled v-model="leaveForm.address"></el-input>
    </el-form-item>
    <el-form-item label="标题：" prop="title">
      <el-input disabled v-model="leaveForm.title"></el-input>
    </el-form-item>
    <el-form-item label="留言内容：" prop="content">
      <el-input disabled type="textarea" v-model="leaveForm.content"></el-input>
    </el-form-item>

    <el-form-item label="回复内容" prop="desc">
      <el-input type="textarea" v-model="leaveForm.replyContent"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="replyForm('leaveForm')">回复</el-button>
      <el-button type="primary" @click="publishForm('leaveForm',1)">发布</el-button>
      <el-button type="primary" @click="publishForm('leaveForm',0)">取消发布</el-button>
      <el-button type="primary" @click="cancelForm">返回</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  props: ['pageType', 'info','type'],
  data() {
    return {
      ruleForm: {
        title: '',
        content: '',
        attachPicList:[],
        type:'',
        ifPublish:0,
      },
      rules: {
        title: [
          { required: true, message: '请输入标题名称', trigger: 'blur' },
          { min: 3, max: 99, message: '长度在 3 到 30 个字符', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请输入内容', trigger: 'blur' },
          { min: 3, max: 5000, message: '长度在 3 到 5000 个字符', trigger: 'blur' }
        ]
      },
      tableData: {},

      // 留言
      leaveForm: {
        name: '',
        createDate: '',
        contact: '',
        email: '',
        address: '',
        title: '',
        content: '',
        replyContent:''
      },
      leaveRules: {
        replyContent: [{ required: true, message: '请填写回复内容', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 留言-----------------------------------------------

    // 发布
    publishForm(formName,flag) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            messageId:this.info.id,
            flag:flag
          }
          this.httpPost('/message/flag',params ).then(ret => {
            this.$message({ type: 'success', message: '操作成功!' })
            this.cancelForm()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 回复
    replyForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            messageId:this.info.id,
            replyContent:this.leaveForm.replyContent
          }
          this.httpPost('/message/reply',params ).then(ret => {
            this.$message({ type: 'success', message: '回复成功!' })
            this.cancelForm()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 留言结束-----------------------------------------------

    // 取消
    cancelForm() {
      this.$emit('changePageType', 0)
    },

    // 发布
    issueForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          params['ifPublish'] = 1
          if (params['attachPicList'] && params['attachPicList'].length > 0)
            params['attachList'] =this.ruleForm['attachPicList'].map((o) => o.response.data.fileUrl).join(',').split(',')
          else
            delete params['attachList']
          console.log(params['attachList'])
          if(this.pageType === 1){
            this.httpPost("/bulletin/add",params).then(res => {
              this.$message.success('发布成功')
              this.cancelForm()
            })
          }else{
            this.httpPost("/bulletin/mod/"+this.info.id,params).then(res => {
              this.$message.success('发布成功')
              this.cancelForm()
            })
          }

        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          console.log(params['attachPicList'])
          if (params['attachPicList'] && params['attachPicList'].length > 0)
            params['attachList'] =this.ruleForm['attachPicList'].map((o) => o.response.data.fileUrl).join(',').split(',')
          else
            delete params['attachList']
          console.log(params['attachList'])
          if(this.pageType === 1){
            this.httpPost("/bulletin/add",params).then(res => {
              this.$message.success('增加成功')
              this.cancelForm()
            })
          }else{
            this.httpPost("/bulletin/mod/"+this.info.id,params).then(res => {
              this.$message.success('修改成功')
              this.cancelForm()
            })
          }

        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    uploadAction() {
      return this.$fileUpload
      // return ''
    },

    uploadSuccess(res, file, fileList) {
      this.ruleForm.attachPicList=fileList
      console.log(this.ruleForm.attachPicList)
    },

    // 删除数组中文件
    handleRemove(file, fileList) {
      console.log(file)
      if (fileList.length != this.ruleForm.attachPicList.length) {
        let material = file.response.data
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.attachPicList.length; i < len; i++) {
          if (this.ruleForm.attachPicList[i].data === material) {
            index = i
            break
          }
        }
        this.ruleForm.attachPicList.splice(index, 1)
        console.log(this.ruleForm.attachPicList)
      }
    },

    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 100MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 富文本
    getCont(val) {
      console.log(val)
      this.ruleForm.content = val
    }
  },

  mounted() {
   this.ruleForm.type=this.type
    if (this.$route.path === '/message') {
      this.httpGet('/message/detail/'+this.info.id, '').then(res => {
        this.leaveForm = res
      })
    } else if (this.pageType === 1) {
      this.$refs.bzlc.editorValue = ''
      this.ruleForm.title = ''
    } else {
      this.httpGet('/bulletin/detail/'+this.info.id, '').then(res => {
        this.ruleForm = res
        this.ruleForm.attachPicList = []
        //TODO 如何通过res.attachList数组显示上传的文件
        if (res['attachList']) {
          res['attachList'].forEach(fn =>
            this.ruleForm.attachPicList.push({
              name: fn.fileName,
              response: {
                data:{
                  fileUrl:fn.fileUrl
                }
              },
              url: this.$picDownload + fn
            })
          )
        }
        console.log(this.ruleForm.attachPicList)
        //富文本
        this.$refs.bzlc.editorValue = this.ruleForm.content
      })
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.upload-demo {
  width: 90px;
  height: auto;
  margin-right: 25px;
  .el-upload-list {
    width: 486px;
    display: block;
  }

  /deep/.el-upload--text {
    width: 90px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 32px;
    border: 1px dashed #ddd;
    border-radius: 4px;
    .el-upload__text {
      font-size: 12px;
    }
    .el-icon-upload {
      display: none;
    }
  }
}
.upload_file {
  display: flex;
  align-items: center;
}
.message_cont {
  .message_item {
    display: flex;
    align-items: center;
    .left {
      width: 100px;
      text-align: right;
      border: 1px solid #ddd;
      padding: 8px 0;
    }
    .right {
      width: 500px;
      padding: 8px 10px;
      border: 1px solid #ddd;
      border-left-width: 0;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    &:not(:last-child) .left {
      border-bottom-width: 0;
    }
    &:not(:last-child) .right {
      border-bottom-width: 0;
    }
  }
}
/deep/.el-form-item {
  input {
    width: 500px;
  }
  textarea {
    width: 500px;
    min-height: 114px !important;
  }
}
  .tips {
    position: absolute;
    top: 0;
    left: 100px;
  }
</style>
